<script>
import "../assets/sass/mine.scss";
export default {
  data() {
    return {
      loginnickname: "请登录",
      loginstate: false,
    };
  },
  created() {
    if (sessionStorage.getItem("loginstate") == "true") {
      this.loginstate = sessionStorage.loginstate;
      this.loginnickname = sessionStorage.getItem("loginnickname");
      this.loginstate = true;
    }
  },
  methods: {
    // 跳转登录注册页面
    register() {
      let state = sessionStorage.getItem("loginstate");
      if (state == "false") {
        this.$dialog
          .confirm({
            title: "声明与政策",
            message:
              "欢迎您来到小米有品！\n我们依据最新法律法规要求，制定并更新了，《隐私政策》 《小米有品用户协议》以及《小米账号使用协议》\n您需阅读并同意相关政策条款方可进行登录。",
            messageAlign: "left",
          })
          .then(() => {
            this.$router.push({ path: "/loginandregister" });
          })
          .catch(() => {
            
          });
      }
    },
    // 退出登录
    quitlogin() {
      sessionStorage.setItem("loginstate", "false");
      this.loginnickname = "请登录";
      this.loginstate = false;
    },
    // 跳转地址管理
    AddressList() {
      let state = sessionStorage.getItem("loginstate");
      if (state == "false") {
        this.$dialog
          .confirm({
            title: "声明与政策",
